import React from "react";

import InfoChart from "./InfoChart";
import Spinner from "../../../utils/spinner";

import { makeStyles } from "@material-ui/core/styles";
import {
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Typography,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    "@media (max-width: 768px)": {
      flexDirection: "column",
    },
    display: "flex",
    margin: "0 auto",
    borderTop: ".5px solid #8c8c8c",
    borderBottom: ".5px solid #8c8c8c",
  },
  Items: {
    width: "100%",
    borderLeft: ".5px solid #8c8c8c",
    borderRight: ".5px solid #8c8c8c",
    "@media (max-width: 768px)": {
      display: "flex",
      justifyContent: "center",
    },
  },
  Text: {
    color: "#fff",
    textAlign: "center",
  },
  Visuals: {
    height: "100%",
    width: "100%",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
  },
  Volume: {
    fontWeight: "bold",
    fontSize: ".7rem",
    textAlign: "center",
    "@media (max-width: 768px)": {
      textAlign: "center",
      fontSize: "1rem",
    },
  },
}));

export default function Info({ markets }) {
  const classes = useStyles();

  return (
    <List className={classes.root}>
      {markets ? (
        markets.slice(0, 3).map((i) => (
          <ListItem className={classes.Items}>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <div style={{ display: "flex", alignItems: "center" }}>
                <ListItemAvatar>
                  <Avatar
                    src={i.image}
                    style={{ scale: "0.5", marginLeft: "13px" }}
                  />
                </ListItemAvatar>
                <ListItemText
                  className={classes.Text}
                  primary={
                    <Typography style={{ fontWeight: "bold" }}>
                      ${i.symbol.toUpperCase()}/USDT
                    </Typography>
                  }
                />
              </div>
              <div>
                <ListItemText
                  className={classes.Text}
                  primary={
                    <Typography
                      style={{ fontWeight: "bold", fontSize: "1.3rem" }}
                    >
                      {`$ ${i.current_price.toLocaleString("en-US")}`}
                    </Typography>
                  }
                  secondary={
                    <Typography
                      style={{
                        color:
                          i.price_change_percentage_24h > 0
                            ? "#008000"
                            : "#ff0000",
                      }}
                    >
                      {`24h ${
                        Math.round(i.price_change_percentage_24h * 100) / 100
                      }%`}
                    </Typography>
                  }
                />
              </div>
            </div>
            <div className={classes.Visuals}>
              <InfoChart coinId={i.id} />
              <Typography className={classes.Volume}>
                Vol: {i.total_volume.toLocaleString("en-US")}
              </Typography>
            </div>
          </ListItem>
        ))
      ) : (
        <div style={{ width: "100$", display: "block" }}>
          <Spinner />
          <p>Sorry, this data is unavailable at the moment</p>
        </div>
      )}
    </List>
  );
}
